.table-holder{
  min-width: 100%;
  overflow-x: auto;
}
.table {
	border-collapse: collapse;
	font-size: 14px;
	width: auto;
	&.deploy-table {
		th,
		td {
			padding: 0 0 0 10px;
		}
		th {
      cursor: pointer;
			&:first-child {
				text-align-last: left;

			}
		}
		td {
			&.provider-item {
				width: 100px;
			}
			&.game-item {
        width: 80px;
        text-align: center;
				display: table-cell;
			}
		}
	}
  &.game-table {
    th {
      cursor: pointer;
    }
  }
	tr {
		height: 55px;
		border-bottom: 1px solid rgba(#445259, .1);
	}
	th, td {
		//padding: 0;
		padding: 27px 0 27px;
		vertical-align: top;
		text-align: left;
		box-sizing: border-box;
		&:not(:first-child) {
			padding: 27px 0 27px 10px;
		}
	}
	th {
		font-weight: 500;
		color: rgba(#445259, .8);
		vertical-align: middle;
		&:first-child {
			text-align: center;
		}
	}
	td {
		color: #000;
		border: 0;
		&.check-item {
			width: 40px;
			text-align: center;
		}
		&.game-item {
      min-width: 80px;
      md-menu{
        min-width: 200px;
      }
			padding: 10px;
			display: flex;
			align-items: center;
			min-height: 54px;
      md-menu {
        display: inline-flex;
      }
		}
		&.skin-item {
			min-width: 85px;
		}
		&.provider-item {
			min-width: 120px;
		}
		&.rtp-item {
			min-width: 65px;
		}
		&.category-item {
			min-width: 90px;
		}
		&.type-item {
			min-width: 95px;
		}
		&.date-item {
			min-width: 120px;
		}
		&.branded-item {
			min-width: 90px;
			text-align: center;
		}
		&.tag-item {
			min-width: 150px;
		}
		&.name-item {
			min-width: 200px;
			min-height: 54px;
			md-menu {
				display: inline-flex;
			}
		}
		&.id-item {
			min-width: 120px;
		}
		&.initialed-item {
			min-width: 140px;
		}
    &.status-item {
      min-width: 80px;
      text-align: center;
    }
	}
	md-checkbox {
		margin: 0 6px 0 0;
		min-width: 18px;
		box-sizing: content-box;
		&:not(.md-checked) {
			.md-icon {
				width: 18px;
				height: 18px;
				border-color: rgba(68, 82, 89, 0.4);
			}
		}
		&.md-checked {
			.md-icon {
				&:after {
					left: 4px;
					top: -1px;
				}
			}
		}
		.md-icon {
			width: 18px;
			height: 18px;
		}
		.md-container {
			width: 18px;
			height: 18px;
			position: relative;
			top: 0;
			transform: translateY(0);
			vertical-align: middle;
		}
		.md-label {
			margin: 0;
		}
	}
	.postfix {
		color: rgba(#445259, .6);
	}
	.material-icons {
		color: #445259;
		&.drop-icon {
			cursor: pointer;
		}
		&.close {
			opacity: 0.4;
		}
	}
	.open-item {
		display: inline-flex;
    align-items: center;
		cursor: pointer;
	}
	.open-icon {
		display: inline-block;
		vertical-align: middle;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 5px 5px 0 5px;
		border-color: rgba(#445259, .5) transparent transparent transparent;
		margin: 0 6px 3px 0;
  }
  .item-ico {
    width: 16px;
    height: 16px;
		margin: 0 6px 0 0;
		> * {
			display: inline-block;
			vertical-align: middle;
		}
	}
	.tooltip-hold {
		height: 16px;
		cursor: pointer;
    margin: 0 0 0 auto;
    &.last {
			margin: 0 0 0 15px;
		}
	}
	.md-button {
		min-width: 50px;
		font-size: 12px;
		margin: 0;
		min-height: 24px;
		line-height: 24px;
		border: 2px solid rgba(68, 82, 89, 0.2);
	}
	.deploy-time {
		display: block;
		font-size: 12px;
		color: rgba(#445259, .6);
	}
}
