collected-values-filter-component,
win-report-filter-component {
  .listing-filter-content {
    width: 250px;
    height: 90%;
    overflow: auto;
    .listing-filter-header {
      display: flex;
      padding: 20px 16px 10px 20px;
      font-size: 18px;
      font-weight: 500;
      color: #5c686f;
      .group-icon {
        cursor: pointer;
        .material-icons {
          font-size: 25px;
        }
      }
    }
    .listing-filter-item {
      padding: 15px 20px 16px;
      border-bottom: 1px solid #eceeee;
      .datepicker-filter {
        md-input-container {
          margin-top: 22px;
        }
      }
      .interval-filter {
        padding-top: 10px;
        display: flex;
      }
      md-input-container {
        margin: 0;
      }
      &.sign-rule-filter-item {
        .filter-params-block {
          display: flex;
          padding-top: 10px;
          .rule-block {
            width: 55%;
          }
        }
      }

      &.custom-filter-item {
        background-color: rgba(240, 169, 44, 0.11);
        border-top: 2px solid #f0a92c;
        .custom-filter {
          display: flex;
          .filter-add-button {
            background-color: inherit;
            margin-left: 10px;
            &:disabled {
              border-color: rgba(68, 82, 89, 0.4);
              opacity: 0.4;
            }
          }
          md-select {
            width: 130px;
          }
        }
      }
      .listing-filter-label {
        font-size: 12px;
        font-weight: 500;
        color: #8f979b;
        text-transform: uppercase;
        padding-bottom: 10px;
      }
      md-checkbox {
        margin: 0 20px 0 0;
      }
      md-select {
        margin: 0;
      }
      md-switch {
        margin-bottom: 0;
        font-size: 14px;
        color: #000000;
        & .md-bar {
          background-color: rgba(68, 82, 89, 0.11);
        }
      }

    }
    .settings {
      .rows-settings,
      .values-settings {
        border-bottom: 1px solid #eceeee;
        padding-left: 10px;
        padding-top: 18px;
        .settings-title {
          font-size: 12px;
          font-weight: 500;
          color: #8f979b;
          text-transform: uppercase;
          padding-left: 10px;
        }
      }
      ul {
        list-style-type: none;
        padding: 0;
        margin: 7px 0 5px;
        li {
          padding-left: 10px;
          font-size: 12px;
          font-weight: normal;
          color: #000000;

          &>span {
            padding-top: 1px;
            margin-right: 5%;
          }
        }
      }

      ul[dnd-list] {
        min-height: 42px;
        padding-left: 0px;
      }
      ul[dnd-list] .dndDraggingSource {
        display: none;
      }
      ul[dnd-list] .dndPlaceholder {
        background-color: #ddd;
        display: block;
        min-height: 42px;
      }
      ul[dnd-list] li {
        display: flex;
        justify-content: space-between;
      }
      ul[dnd-list] li.selected {
        background-color: #dff0d8;
        color: #3c763d;
      }
      ul[dnd-list] {
        md-checkbox .md-container:after {
          display: none;
        }
      }
      ul[dnd-list] {
        md-checkbox {
          margin: 8px 0;
        }
      }
    }
    .datepicker-field {
      margin: 0;
      width: 99%;
      input {
        cursor: pointer;
      }
      .material-icons {
        color: #a8adaf;
        position: absolute;
        font-size: 20px;
        right: 0;
      }
    }
    .filter-add-button {
      width: 60px;
      height: 28px;
      border-radius: 18px;
      border: solid 2px #f0a92c;
      background: #fff;
      color: #445259;
      font-size: 12px;
      font-weight: 500;
      line-height: 25px;
      min-width: 60px;
      min-height: 28px;
      margin: 0;
    }
    .closed {
      background-color: #fffdf2;
    }
    md-checkbox.md-checked .md-icon {
      background-color: #000000;
    }
    md-checkbox:not(.md-checked) .md-icon {
      opacity: 0.4;
    }
    md-checkbox.md-indeterminate .md-icon:after {
      width: 7px;
      height: 7px;
      border: none;
      background-color: #b4b9bc;
      border-radius: 100%;
    }
  }
}


