$switch-thumb-default: #66737a;
$switch-thumb-active: #253035;
$switch-default: #ecedee;
$switch-active: #b4babd;

// @import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500,600,700');
@import './reportsStyles/index.scss';

*[role="button"] {
  outline: none;
}

body,
html {
  #ModOperatorgamedeploy {
    font-family: 'Roboto';
    font-size: 14px;
    min-height: 800px;
  }
  label {
    margin: 0;
  }
}

md-icon {
  width: 16px;
  height: 18px;
  min-width: 16px;
  min-height: 18px;
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 16px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  -webkit-font-feature-settings: 'liga';
  -webkit-font-smoothing: antialiased;
}

html,
body {
  width: 100%;
  height: auto;
  background-color: #ffffff;
}

button.md-button {
  border-radius: 20px;
  text-transform: capitalize;
  &.apply-button {
    float: right;
  }
}

.md-select-value .md-select-icon {
  margin: 0;
  &:after {
    font-size: 16px;
  }
}

md-input-container {
  margin-left: 20px;
  &.with-prefix {
    display: flex;
    align-items: flex-end;
    .input-postfix {
      margin-left: 5px;
      margin-bottom: 6px;
    }
  }
  .md-errors-spacer {
    float: right;
    min-height: 0px;
  }
}

.face-placeholder {
  font-family: Roboto;
  color: rgba(0, 0, 0, 0.54);
  font-weight: 300;
  position: absolute;
  font-size: 13.5px;
  top: -13px;
  order: 1;
}

.md-input-messages-animation {
  top: 100%;
}

md-input-container .md-input-messages-animation {
  position: absolute;
}

.custom-tab {
  margin-bottom: 15px;
  .custom-tab-selector {
    border-radius: 20px;
    box-shadow: 0 0 10px 0 rgba(240, 169, 44, 0.08);
    display: inline-flex;
    overflow: hidden;
    .tab-selector-item {
      cursor: pointer;
      font-size: 14px;
      padding: 7px 15px;
      background-color: #fff;
      color: #445259;
      &.active {
        color: #fff;
        background-color: #445259;
      }
    }
  }
}

md-switch {
  &.custom-switch {
    .md-bar {
      background-color: $switch-default;
    }
    .md-thumb {
      background-color: $switch-thumb-default;
    }
    &.md-checked {
      .md-bar {
        background-color: $switch-active;
      }
      .md-thumb {
        background-color: $switch-thumb-active;
      }
    }
  }
  &.custom-grey-switch {
    .md-bar {
      background-color: #e9eaeb;
    }
    &.md-checked {
      .md-bar {
        background-color: $switch-active;
      }
      .md-thumb {
        background-color: $switch-thumb-active;
      }
    }
    &.little {
      .md-thumb-container {
        top: 0;
        width: 20px;
      }
      .md-thumb {
        width: 16px;
        height: 16px;
      }
      .md-bar {
        height: 8px;
      }
    }
  }
  &.light-gray-switch {
    .md-bar {
        background-color: #445259;
        opacity: .1;
    }
    .md-thumb {
        box-shadow: 0 0 10px 0 rgba(37, 48, 53, 0.25);
    }
}
}

.md-backdrop.md-opaque {
  opacity: .9;
}

.addition {
  color: rgba(68, 82, 89, 0.6);
}

.addition::before {
  content: " ";
  white-space: pre;
}

.template-selector {
  padding-left: 15px;
  padding-right: 5px;
  box-shadow: 0 0 18px 0 rgba(0, 0, 0, 0.04);
  background-color: #fff; //min-width: 140px;
  margin: 0;
  border-radius: 15px;
  margin-left: 10px;
  md-select-value.md-select-value {
    border: none;
  }
}

.report-empty-response {
  text-align: center;
  margin: 40px auto;
  font-size: 16px;
  text-transform: uppercase;
  font-weight: 800;
}

md-tabs.md-default-theme .md-tab,
md-tabs .md-tab {
  padding: 12px;
}

*::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
*::-webkit-scrollbar-button {
  width: 0px;
  height: 0px;
}
*::-webkit-scrollbar-thumb {
  background: #ecedee;
  border: 0px none #ffffff;
  border-radius: 50px;
}
*::-webkit-scrollbar-thumb:hover {
  background: #ecedee;
}
*::-webkit-scrollbar-thumb:active {
  background: #ecedee;
}
*::-webkit-scrollbar-track {
  background: #ffffff;
  border: 0px none #ffffff;
  border-radius: 50px;
}
*::-webkit-scrollbar-track:hover {
  background: #ffffff;
}
*::-webkit-scrollbar-track:active {
  background: #fff;
}
*::-webkit-scrollbar-corner {
  background: transparent;
}
