.progressive-preloader {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  .progressive-preloader-liner {
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    position: absolute;
  }
  md-progress-circular.md-default-theme path,
  md-progress-circular path {
    stroke: rgb(253, 165, 0);
  }
}
