@import '../node_modules/angular-material-data-table/dist/md-data-table.css';
@import './styles/variables.scss';
@import './styles/themes.scss';
@import '../node_modules/angularjs-toaster/toaster.min.css';
@import '../node_modules/jsonformatter/dist/json-formatter.css';

body,
body#bo.isoftbet.com,
html {
  height: 100%;
}

input:focus:invalid:focus,
textarea:focus:invalid:focus,
select:focus:invalid:focus,
textarea,
input[type="text"],
input[type="password"],
input[type="datetime"],
input[type="datetime-local"],
input[type="date"],
input[type="month"],
input[type="time"],
input[type="week"],
input[type="number"],
input[type="email"],
input[type="url"],
input[type="search"],
input[type="tel"],
input[type="color"],
.uneditable-input {
  box-shadow: none;
}

textarea:focus,
input[type="text"]:focus,
input[type="password"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="time"]:focus,
input[type="week"]:focus,
input[type="number"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="search"]:focus,
input[type="tel"]:focus,
input[type="color"]:focus,
.uneditable-input:focus {
  box-shadow: none;
  border-color: inherit;
}

select,
textarea,
input[type="text"],
input[type="password"],
input[type="datetime"],
input[type="datetime-local"],
input[type="date"],
input[type="month"],
input[type="time"],
input[type="week"],
input[type="number"],
input[type="email"],
input[type="url"],
input[type="search"],
input[type="tel"],
input[type="color"],
.uneditable-input {
  margin-bottom: 0;
  color: rgba(0,0,0,0.87);
}

.md-tabs md-ink-bar {
  color: $yellow;
  background: $yellow;
}

.containerWrapper td {
  vertical-align: inherit;
}

.md-tooltip {
  height: auto !important;
}

.moduleNav.nav.nav-tabs{
  z-index: 10;
  position: absolute;
  right: 16px;
  top: 61px;
  border-bottom: none;
}
