collected-grand-total,
win-report-grand-total {
  display: block;
  width: 100%;
  .grand-total-item {
    background-color: #fbfcfc;
    border-bottom: 1px solid rgba(68, 82, 89, 0.1);
    border-top: 1px solid rgba(68, 82, 89, 0.1);
    .collected-current-row{
      margin-left: 20px;
      padding-right: 10px;
    }
    .collected-row-name {
      padding-left: 20px;
      margin-right: -20px;
    }
  }

  .total-label{
    padding-right: 20px;
  }

  .total-currency{
    font-weight: 400;
    color: rgba(68, 82, 89, 0.6);
    font-size: 14px;
  }
}
