collected-values-table-component,
win-report-table-component {
  overflow: auto;
  display: block;
  .collected-values-table {
    display: table-caption;
    width: 1px;
    collected-rec-row+collected-rec-row {
      &:before {
        width: calc(100% - 20px);
        content: '';
        height: 1px;
        background-color: rgba(68, 82, 89, 0.1);
        display: block;
        position: relative;
        left: 20px;
      }
    }
    .table-head {
      border-bottom: 1px solid rgba(68, 82, 89, 0.1);
      height: 55px;
      font-size: 14px;
      color: rgba(68, 82, 89, 0.8);
      align-items: center;
      display: inline-flex;
      .collected-row-name,
      .collected-row-count,
      .collected-row-value,
      .collected-row-params {
        font-weight: 600;
      }
      .collected-row-name {
        width: 260px;
      }
      .collected-row-count {
        width: 80px;
        text-align: center;
      }
      .collected-row-value {
        width: 160px;
        text-align: right;
      }
      .collected-row-params {
        width: 100px;
        text-align: center;
      }
    }
  }
  .collected-rec-row {
    flex-wrap: wrap;
    display: inline-flex;
    .collected-rec-row {
      margin-left: 20px;
    }
    &.row-deep-7 {
      background-color: #fff5e2;
    }
    &.row-deep-6 {
      background-color: #fffae0;
    }
    &.row-deep-5 {
      background-color: #fffbe6;
    }
    &.row-deep-4 {
      background-color: #fffdf2;
    }
    &.row-deep-3 {
      background-color: #f4fbfd;
    }
    &.row-deep-2 {
      background-color: #f8fdff;
    }
    &.row-deep-1 {
      background-color: #ffffff;
    }
  }
  .collected-current-row {
    display: flex;
    min-height: 55px;
    align-items: center;
    font-size: 14px;
    width: 100%;
    .currency-label {
      color: rgba(68, 82, 89, 0.6);
    }
    .group-icon {
      cursor: pointer;
      margin: 0 10px;
      .material-icons {
        color: #000;
        font-size: 18px;
        &.isClosed {
          transform: rotate(180deg);
        }
      }
    }
    .row-index {
      width: 30px;
      text-align: center;
    }
    .collected-row-name {
      box-sizing: border-box;
      width: 260px;
      color: #445259;
      display: flex;
      font-weight: 600;
      word-break: break-word;
      padding-right: 20px;
    }
    .collected-row-count {
      width: 80px;
      color: #000;
      text-align: center;
    }
    .collected-row-value {
      color: #000;
      width: 160px;
      text-align: right;
      padding-right: 5px;
      box-sizing: border-box;
    }
    .collected-row-params {
      width: 100px;
      text-align: center;
    }
  }
  .deep-visible-params-6,
  .deep-visible-params-5,
  .deep-visible-params-4,
  .deep-visible-params-3,
  .deep-visible-params-2,
  .deep-visible-params-1 {
    display: none;
  }

  .deev-visible-parent-1 {
    .deep-visible-params-1 {
      display: block;
    }
  }
  .deev-visible-parent-2 {
    .deep-visible-params-1,
    .deep-visible-params-2 {
      display: block;
    }
  }
  .deev-visible-parent-3 {
    .deep-visible-params-1,
    .deep-visible-params-2,
    .deep-visible-params-3 {
      display: block;
    }
  }
  .deev-visible-parent-4 {
    .deep-visible-params-1,
    .deep-visible-params-2,
    .deep-visible-params-3,
    .deep-visible-params-4 {
      display: block;
    }
  }
  .deev-visible-parent-5 {
    .deep-visible-params-1,
    .deep-visible-params-2,
    .deep-visible-params-3,
    .deep-visible-params-4,
    .deep-visible-params-5 {
      display: block;
    }
  }

  .deev-visible-parent-6 {
    .deep-visible-params-1,
    .deep-visible-params-2,
    .deep-visible-params-3,
    .deep-visible-params-4,
    .deep-visible-params-5,
    .deep-visible-params-6 {
      display: block;
    }
  }
}
